import React, { Component } from 'react';
import {Form,Button,InputGroup,Col,Image} from 'react-bootstrap';
import './bootstrap.css';
import './Contact.css';
import imgTitle from "./assets/ic_title.png";
import imgName from "./assets/ic_name.png";
import imgEmail from "./assets/ic_email.png";
import MainFooter from "./MainFooter";

class Contact extends Component {
    constructor(...args) {
        super(...args);

        this.state = { validated: false };
    }

    handleSubmit(event) {
        const form = event.currentTarget;
        if (form.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
        this.setState({ validated: true });
    }

    render() {
        const { validated } = this.state;
        return (
            <div className="body">
            <Form className="form"
                  noValidate
                  validated={validated}>
                <Form.Row>
                    <Form.Group as={Col} md="6" controlId="validationCustom01">
                        <Form.Label>Nome</Form.Label>
                        <InputGroup>
                            <Form.Control
                                required
                                type="text"
                                placeholder=""
                                defaultValue=""
                            />
                            <InputGroup.Prepend>
                                <InputGroup.Text id="inputGroupPrepend"><Image src={imgName} style={{height:"15px"}}/></InputGroup.Text>
                            </InputGroup.Prepend>

                            <Form.Control.Feedback type="invalid">
                                Por favor, digite um nome.
                            </Form.Control.Feedback>
                            <Form.Control.Feedback>Ok</Form.Control.Feedback>
                        </InputGroup>
                    </Form.Group>
                    <Form.Group as={Col} md="6" controlId="validationCustomUsername">
                        <Form.Label>E-mail</Form.Label>
                        <InputGroup>
                            <Form.Control
                                type="email"
                                placeholder=""
                                aria-describedby="inputGroupPrepend"
                                required
                            />
                            <InputGroup.Prepend> <InputGroup.Text id="inputGroupPrepend"><Image src={imgEmail} style={{height:"15px"}}/></InputGroup.Text>

                            </InputGroup.Prepend>
                            <Form.Control.Feedback type="invalid">
                                Por favor, digite seu email.
                            </Form.Control.Feedback>
                        </InputGroup>
                    </Form.Group>
                </Form.Row>
                <Form.Row>
                    <Form.Group as={Col} controlId="validationCustom03">
                        <Form.Label>Título</Form.Label>
                        <InputGroup>
                            <Form.Control type="text" placeholder="" required />
                            <InputGroup.Prepend>
                                <InputGroup.Text id="inputGroupPrepend"><Image src={imgTitle} style={{height:"15px"}}/></InputGroup.Text>
                            </InputGroup.Prepend>
                            <Form.Control.Feedback type="invalid">
                                Por favor, escolha um título.
                            </Form.Control.Feedback>
                        </InputGroup>
                    </Form.Group>
                </Form.Row>
                <Form.Row>
                    <Form.Group  as={Col} controlId="validationCustom04">
                        <Form.Label>Mensagem</Form.Label>
                        <InputGroup>
                            <Form.Control as="textarea" rows="3" placeholder="" required />
                            <Form.Control.Feedback type="invalid">
                                Não esqueça de deixar sua mensagem.
                            </Form.Control.Feedback>
                        </InputGroup>
                    </Form.Group>
                </Form.Row>
                <Form.Group>
                    <Form.Check
                        label="Enviar cópia para meu E-mail"
                        feedback=""
                    />
                </Form.Group>
                <Button type="button" onClick={e => this.handleSubmit(e)}>Submit form</Button>
            </Form>
                <MainFooter  className='contactFooter'/>
            </div>
        );
    }
}


export default Contact;